const baseEndpoint = 'https://ghibliapi.herokuapp.com/films';
const proxy = 'https://cors-anywhere.herokuapp.com/';
const form = document.querySelector(`.form_input`);
const moviesEl = document.querySelector(`.movies`);

async function fetchFilms(movie) {
    const response = await fetch(`${proxy}${baseEndpoint}?q=${movie}`);
    const data = await response.json();
    return data;
}

const handleSubmit = async event => {
    event.preventDefault();
    const filmEl = event.currentTarget;
    const movies = await fetchFilms(filmEl.movie.value);
    filmEl.submit.disabled = false;
    console.log(movies);
    displayFilms(movies.results);
    
}

function displayFilms(movies) {
    const html = movies.map(movie => {
        return `
           <div class="movie">
                <h2>${movie.title}</h2>
                <span class="release_date">${movie.release_date}</span>
                <span class="score">${movie.rt_score}</span>
                <p class="description">
                 ${movie.description}
                </p>
                <ul class="more">
                    <li>${movie.director}</li>
                    <li>${movie.producer}</li>
                </ul>
           </div>
        `;
    });
    moviesEl.innerHTML = html.join('');
};
displayFilms();
form.addEventListener('submit', handleSubmit);
fetchFilms('Castle in the Sky');